@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&family=Staatliches&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&family=Staatliches&display=swap');

.App {
  text-align: center;
}

*{
  background-color: #F1E8B8;
  
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #70A0AF;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h3 {
  color:  white;
  font-weight: 800;
  padding-bottom: 40px;
  font-size: x-large;
}

.header {
  font-size: 200px;
  margin: 40px;
  color: #ffffff;
}

button {
  height: 100px;
  width: 300px;
  font-family: 'Courier New', Courier, monospace;
  font-family: 'Open Sans', sans-serif;
  font-family: 'Staatliches', cursive;
  color: white;
  background-color: #412722;
  font-size: 40px;
  border-radius: 40px;
  box-shadow:inset 0 0 0 #590004;
  transition: ease-out .5s;
}



button:hover {
  box-shadow:inset 300px 0 0 #DE6B48;
  cursor: pointer;
}

.container {
  display: flex;
  flex-direction: column;
  color: white;
  align-items: center;
  justify-items: center;
}